<template> 
  <div class="personal">
    <div class="header">
      <div class="header_name">
        <span class="userName">{{ userInfo.comp.storeName }}</span>
        <span class="icon">
            <img src="../../assets/me_icon/xiaoxi.png" @click="toPages('message')" />
            <span :class="{'mask':isExistMessage }"></span>
            <img src="../../assets/me_icon/shezhi.png" @click="toPages('set')" />
        </span>
      </div>
      <div class="countInfo_0">
        <span class="authentication">
          <img src="../../assets/me_icon/yirenzhen.png" />
          <span>
            {{ userInfo.comp.authenticationMessage }}
          </span>
        </span>
        <span class="userLv">
          <img src="../../assets/me_icon/huiyuan.png" />
          <span>{{ userLv.levelname }}</span>
        </span>
      </div>
      <ul class="countInfo_1">
        <li class="balance">
          <p>{{ balance }}</p>
          <p>我的余额</p>
        </li>
        <li class="coupon"  @click="toPages('coupons')">
          <p>{{ countInfo[0] }}</p>
          <p>我的优惠券</p>
        </li>
        <li class="integral" @click="toPages('sign')">
          <p>{{ countInfo[1] }}</p>
          <p>我的积分</p>
        </li>
      </ul>
    </div>
    <div class="content">
      <div class="order">
        <div class="myOrder">
          <span>我的订单</span>
          <span @click="goOrderList(0)">全部订单 ></span>
        </div>
        <ul>
          <li @click="goOrderList(1)">
            <span v-show="countInfo[2] > 0 ">{{ countInfo[2] }}</span>
            <img src="../../assets/me_icon/daifukuan.png" />
            <p>待付款</p>
          </li>
          <li @click="goOrderList(2)">
            <span v-show="countInfo[3] > 0 ">{{ countInfo[3] }}</span>
            <img src="../../assets/me_icon/daifahuo.png" />
            <p>待发货</p>
          </li>
          <li @click="goOrderList(3)">
            <span v-show="countInfo[4] > 0 ">{{ countInfo[4] }}</span>
            <img src="../../assets/me_icon/daifahuo2.png" />
            <p>待收货</p>
          </li>
          <li @click="goOrderList(4)">
            <span v-show="countInfo[5] > 0 ">{{ countInfo[5] }}</span>
            <img src="../../assets/me_icon/yifahuo3.png" />
            <p>已收货</p>
          </li>
          <li @click="goServiceAfterSalesRecord">
            <span v-show="countInfo[6] > 0 ">{{ countInfo[6] }}</span>
            <img src="../../assets/me_icon/shouhoujilu.png" />
            <p>售后记录</p>
          </li>
        </ul>
      </div>
      <div class="tools">
        <p class="title">常用工具</p>
        <ul>
          <li>
            <img src="../../assets/me_icon/caigou.png" @click="plan" />
            <p>采购计划</p>
          </li>
          <li @click="toPages('manageConsignee')">
            <img src="../../assets/me_icon/shouhuoguanli.png" />
            <p>收货管理</p>
          </li>
          <li @click="toPages('myCollection')">
            <img src="../../assets/me_icon/shoucang.png" />
            <p>我的收藏</p>
          </li>
          <li @click="toPages('myFoot')">
            <img src="../../assets/me_icon/zuji.png" />
            <p>我的足迹</p>
          </li>
          <li @click="toPages('myBd')" class="li_5">
            <img src="../../assets/me_icon/zhuanshushangwu.png" />
            <p>专属商务</p>
          </li>
          <li @click="toPages('pharmacyInfo')">
            <img src="../../assets/me_icon/yaodianxinxi.png" />
            <p>药店信息</p>
          </li>
          <li @click="toPages('myInvoice')">
            <img src="../../assets/me_icon/zizhiguanli.png" />
            <p>我的发票</p>
          </li>
        </ul>
      </div>
      <div class="help">
        <p class="title">帮助中心</p>
        <ul>
          <li>
            <img src="../../assets/me_icon/shouhouguize.png" @click="toPages('after_ru')"/>
            <p>退款规则</p>
          </li>
          <li>
            <img src="../../assets/me_icon/fuwutiaokuan.png"  @click="toPages('server_ru')"/>
            <p>服务条款</p>
          </li>
          <li>
            <img src="../../assets/me_icon/huodongguize.png" @click="toPages('active_ru')" />
            <p>活动规则</p>
          </li>
          <li @click="toPages('serverCenter')">
            <img src="../../assets/me_icon/wenti.png" />
            <p>服务中心</p>
          </li>
          <li class="li_5" @click="$openTel(serviceQQ)">
            <img src="../../assets/me_icon/kefu.png" />
            <p>在线客服</p>
          </li>
          <li @click="toPages('wishList')">
            <img src="../../assets/me_icon/xinyuandan.png"/>
            <p>心愿单</p>
          </li>
        </ul>
      </div>
      <div class="customerService" >
        <p @click="callPhone">客服电话：{{tel}}</p>
      </div>
    </div>
  </div>
</template>
<script>
import * as types from "@/store/mutations"

export default {
  data() {
    return {
      countInfo: [0,0,0,0,0],
      userLv: {},
      balance: 0,
      tel:null,
      serviceQQ:null,
      compidInfo: {},
    }
  },

  computed: {
    isExistMessage(){
      return this.$store.state.message.length >0
    },
    userInfo() {
      return this.$loadUserInfo();
    }
  },
  created() {
    this.$store.commit(types.SET_TABBAR_INDEX,3)
  },
  mounted() {
    this.getServiceInfo()
  //   this.getCountInfo()
    this.getLv()
  //   this.getBalance()
  },
  activated() {
    this.getCountInfo()
    this.getBalance()
  },
  methods: {
    heart(){
      this.$toast('功能正在完善,敬请期待')
    },
    plan(){
      this.$toast('功能正在完善,敬请期待')
    },
    toPages(name) {
      this.$router.push({
        name: name
      })
    },
    //  获取发货收货数量
    getCountInfo() {
      JNB.query({
        serverName: 'orderServer'+this.$ser_in(this.userInfo.comp.storeId),
        cls: 'OrderInfoModule',
        method: 'appCountCompInfo',
        params: {
          param: this.userInfo.comp.storeId
        },
        callback: (data)=>{
          let result = JSON.parse(data)
          if (result.code === 200) {
          // 更新用户登录状态
            this.countInfo = result.data
          }else {
            this.$toast.fail(result.message)
          }
        }
      })
    },
    //  获取等级
    getLv() {
      JNB.query({
        serverName: 'userServer',
        cls: 'MemberModule',
        method: 'getMember',
        params: {
          compid: this.userInfo.comp.storeId
        },
        callback: (data)=>{
          let result = JSON.parse(data)
          if (result.code === 200) {
            // 更新用户登录状态
            this.userLv = result.data
          }else {
            this.$toast.fail(result.message)
          }
        }
      })
    },
    //  获取余额
    getBalance() {
      JNB.query({
        serverName: 'discountServer',
        cls: 'CouponManageModule',
        method: 'queryCompAllBal',
        params: {
          compid: this.userInfo.comp.storeId
        },
        callback: (data)=>{
          let result = JSON.parse(data)
          if (result.code === 200) {
            // 更新用户登录状态 
            this.balance = result.data.balamt
          }else {
            this.$toast.fail(result.message)
          }
        }
      })
    },
    //去订单列表
    goOrderList(index){
      this.$router.push({
        path: '/orderList',
        query:{
          index:index
        }
      })
    },
    //去售后记录
    goServiceAfterSalesRecord(){
      this.$router.push({
        path: '/serviceAfterSalesRecord'
      })
    },
    //拨打客服热线
    callPhone(){
      JNB._requestNative("callPhone", this.tel);
    },
    //获取客服信息
    getServiceInfo(){
      JNB.query({
        serverName: "globalServer",
        cls: "WebAppModule",
        method: "customerInfo",
        callback :(data)=>{
          data = JSON.parse(data);
          if(data.code == 200){
            data.data = JSON.parse(data.data);
            this.tel = data.data.compTel;
            this.serviceQQ = data.data.customList[Math.floor(Math.random())*data.data.customList.length].qq;
          }
        },
      })
    }
  }
}
</script>
